.container{
    height: 100%;
}

.MenuButton{
    width: 50px;
    height: 50px;
    float: right;
    margin: 15px;
}

.MenuLogo{
    height: 85px;
    float: left;
    margin-left: 15px;
}

.NavBar{
    height: 85px;
    background-image: url("../../Assets/Bordures/Bordures__ecole.png");
    background-size: 101vw;
    flex-direction: row;
    background-position: center;
    justify-content: center;
}

.Menu{
    display: grid;
    grid-template-columns: repeat(6,85px);
    grid-auto-rows: 80px;
    justify-content: end;
}

.MenuV {
    display: grid;
    grid-template-columns: repeat(1, 200px);
    justify-content: center;
    grid-auto-rows: 85px;
}

.MenuItem{
    background-image:url("../../Assets/Menu/Menu_button_bc.png");
    background-repeat:no-repeat;
    background-position: center;
    height:100%;
    background-size: contain;
    font-size: 15px;
    color: white;
}

.MenuItemV{
    background-image:url("../../Assets/Menu/Menu_button_bcV.png");
    background-repeat:no-repeat;
    background-position: center;
    height:100%;
    background-size: contain;
    font-size: 15px;
    color: white;
}




.MenuItemEnd{
    background-image:url("../../Assets/Menu/Menu_button_bc_end.png");
}
.MenuItemEndV{
    background-image:url("../../Assets/Menu/Menu_button_bcV_end.png");
}


.MenuItemStart{
    background-image:url("../../Assets/Menu/Menu_button_bc_start.png");
}
.MenuItemStartV{
    background-image:url("../../Assets/Menu/Menu_button_bcV_start.png");
}

.MenuItem.MenuItemEnd:hover{
    background-image:url("../../Assets/Menu/Menu_button_bc_end_filled.png");
}
.MenuItemV.MenuItemEndV:hover{
    background-image:url("../../Assets/Menu/Menu_button_bcV_end_filled.png");
}

.MenuItem.MenuItemStart:hover{
    background-image:url("../../Assets/Menu/Menu_button_bc_start_filled.png");
}
.MenuItemV.MenuItemStartV:hover{
    background-image:url("../../Assets/Menu/Menu_button_bcV_start_filled.png");
}

.MenuItem:hover{
    background-image:url("../../Assets/Menu/Menu_button_bc_filled.png");
}

.MenuItemV:hover{
    background-image:url("../../Assets/Menu/Menu_button_bcV_filled.png");
}

.MenuItem a{
    display: flex;
    height: 100%;
    justify-content: start;
    flex-direction: column;
    color: #fff;
}

.MenuItemV a{
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    text-align: left;
    color: #fff;
}

.MenuItem a.Impair{
    justify-content: end;
}

.MenuItemV a.Impair{
    text-align: right;
}

